.form {
  width: 280px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;

  @media (min-width: $tablet-width) {
    width: 709px;
    padding-left: 30px;
    padding-right: 30px;
  }

  @media (min-width: $desktop-width) {
    width: 1220px;
    padding-left: 110px;
    padding-right: 110px;
  }
}

.form__button {
  width: 280px;
  border: 0;
  margin-bottom: 21px;

  @media (min-width: $desktop-width) {
    width: 565px;
  }
}

.form__legend {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-style: italic;
  font-size: 24px;
  line-height: 26px;
  padding-top: 26px;
  margin-bottom: 35px;

  @media (min-width: $tablet-width) {
    font-size: 36px;
    line-height: 36px;
  }
}

.form__list {
  list-style-type: none;
  padding: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-style: italic;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 18px;
  color: #444444;
  margin: 0;
}

.form__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: italic;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
  width: 226px;
  margin: 0;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;
    padding-bottom: 66px;
    width: 323px;
  }
}

.form__fields {
  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;
    padding-bottom: 66px;
    width: 630px;
  }

  @media (min-width: $desktop-width) {
    width: 1220px;
  }
}

.form__interview-conteiner {
  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 569px 568px;
    column-gap: 80px;
    padding-bottom: 69px;
  }
}

.form__label {
  margin-right: 12px;

  @media (min-width: $tablet-width) {
    font-size: 20px;
  }
}

.form__item {
  height: 45px;
  margin-bottom: 14px;
  display: flex;
  align-items: center;
}

.form__input {
  padding-top: 13px;
  padding-bottom: 14px;
  padding-left: 20px;
  padding-right: 0;
  border: 1px solid #e7e7e7;
  min-width: 153px;
  flex-grow: 1;
}

.form__wrapper {
  width: 280px;
  margin: 0 auto;


  @media (min-width: $tablet-width) {
    width: 709px;
  }

  @media (min-width: $desktop-width) {
    width: 1220px;
  }
}

.form__fieldset {
  border: none;
  padding: 0;
}

.form__comments {
  width: 280px;
  min-height: 150px;
  border: 1px solid #e7e7e7;
  border-radius: 0;

  @media (min-width: $tablet-width) {
    width: 630px;
  }

  @media (min-width: $desktop-width) {
    width: 1220px;
    height: 174px;
  }
}

.form__note {
  display: inline;
  text-align: center;
}
